<script setup lang="ts">
import { ref, reactive, computed, onMounted } from 'vue';

// --- Ê∏∏ÊàèÈÖçÁΩÆ ---
const ROWS = 9;
const COLS = 9;
const MINES = 10;

// --- Á±ªÂûãÂÆö‰πâ ---
interface Cell {
  isMine: boolean;
  isRevealed: boolean;
  isFlagged: boolean;
  neighborMines: number; // Áõ∏ÈÇªÂú∞Èõ∑Êï∞
}

// --- ÂìçÂ∫îÂºèÁä∂ÊÄÅ ---
const board = reactive<Cell[][]>([]);
const gameOver = ref(false);
const gameWon = ref(false);
const firstClick = ref(true);

// --- ËÆ°ÁÆóÂ±ûÊÄß ---
const totalCells = computed(() => ROWS * COLS);
const revealedCount = computed(() => {
  let count = 0;
  for (let r = 0; r < ROWS; r++) {
    for (let c = 0; c < COLS; c++) {
      // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
      const row = board[r];
      if (row) {
        const cell = row[c];
        if (cell && cell.isRevealed) count++;
      }
    }
  }
  return count;
});

// --- ÂàùÂßãÂåñÊ£ãÁõò ---
const initializeBoard = () => {
  board.length = 0; // Clear existing board
  for (let r = 0; r < ROWS; r++) {
    const row: Cell[] = [];
    for (let c = 0; c < COLS; c++) {
      row.push({
        isMine: false,
        isRevealed: false,
        isFlagged: false,
        neighborMines: 0
      });
    }
    board.push(row);
  }
  gameOver.value = false;
  gameWon.value = false;
  firstClick.value = true;
};

// --- ÊîæÁΩÆÂú∞Èõ∑ ---
const placeMines = (firstClickRow: number, firstClickCol: number) => {
  let minesPlaced = 0;
  while (minesPlaced < MINES) {
    const r = Math.floor(Math.random() * ROWS);
    const c = Math.floor(Math.random() * COLS);
    // Á°Æ‰øùÁ¨¨‰∏Ä‰∏™ÁÇπÂáªÁöÑÊ†ºÂ≠êÂèäÂÖ∂Âë®Âõ¥‰∏çÊòØÂú∞Èõ∑
    const isFirstClickArea =
      Math.abs(r - firstClickRow) <= 1 && Math.abs(c - firstClickCol) <= 1;
    // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
    const row = board[r];
    if (row) {
      const cell = row[c];
      if (cell && !cell.isMine && !isFirstClickArea) {
        cell.isMine = true;
        minesPlaced++;
      }
    }
  }
};

// --- ËÆ°ÁÆóÁõ∏ÈÇªÂú∞Èõ∑Êï∞ ---
const calculateNeighborMines = () => {
  for (let r = 0; r < ROWS; r++) {
    for (let c = 0; c < COLS; c++) {
      // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
      const row = board[r];
      if (row) {
        const cell = row[c];
        if (cell && !cell.isMine) {
          let count = 0;
          for (let dr = -1; dr <= 1; dr++) {
            for (let dc = -1; dc <= 1; dc++) {
              if (dr === 0 && dc === 0) continue;
              const nr = r + dr;
              const nc = c + dc;
              // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[nr][nc]Â≠òÂú®
              if (nr >= 0 && nr < ROWS && nc >= 0 && nc < COLS) {
                const nRow = board[nr];
                if (nRow) {
                  const nCell = nRow[nc];
                  if (nCell && nCell.isMine) {
                    count++;
                  }
                }
              }
            }
          }
          cell.neighborMines = count;
        }
      }
    }
  }
};

// --- ÁÇπÂáªÊ†ºÂ≠ê ---
const revealCell = (r: number, c: number) => {
  // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
  const row = board[r];
  if (!row) return;
  
  const cell = row[c];
  if (!cell) return;
  
  if (gameOver.value || gameWon.value || cell.isRevealed || cell.isFlagged) return;

  if (firstClick.value) {
    placeMines(r, c);
    calculateNeighborMines();
    firstClick.value = false;
  }

  cell.isRevealed = true;

  if (cell.isMine) {
    gameOver.value = true;
    revealAllMines();
    return;
  }

  if (cell.neighborMines === 0) {
    // Auto-reveal neighbors if it's an empty cell
    for (let dr = -1; dr <= 1; dr++) {
      for (let dc = -1; dc <= 1; dc++) {
        if (dr === 0 && dc === 0) continue;
        const nr = r + dr;
        const nc = c + dc;
        // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[nr][nc]Â≠òÂú®
        if (nr >= 0 && nr < ROWS && nc >= 0 && nc < COLS) {
          const nRow = board[nr];
          if (nRow) {
            const nCell = nRow[nc];
            if (nCell && !nCell.isRevealed && !nCell.isFlagged) {
              revealCell(nr, nc); // Recursive reveal
            }
          }
        }
      }
    }
  }

  checkWin();
};

// --- Êè≠Á§∫ÊâÄÊúâÂú∞Èõ∑ (Ê∏∏ÊàèÁªìÊùüÊó∂) ---
const revealAllMines = () => {
  for (let r = 0; r < ROWS; r++) {
    for (let c = 0; c < COLS; c++) {
      // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
      const row = board[r];
      if (row) {
        const cell = row[c];
        if (cell && cell.isMine) {
          cell.isRevealed = true;
        }
      }
    }
  }
};

// --- Ê£ÄÊü•ÊòØÂê¶Ëé∑ËÉú ---
const checkWin = () => {
  if (revealedCount.value === totalCells.value - MINES) {
    gameWon.value = true;
    flagAllMines();
  }
};

// --- Ê†áËÆ∞ÊâÄÊúâÂú∞Èõ∑ (Ëé∑ËÉúÊó∂) ---
const flagAllMines = () => {
  for (let r = 0; r < ROWS; r++) {
    for (let c = 0; c < COLS; c++) {
      // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
      const row = board[r];
      if (row) {
        const cell = row[c];
        if (cell && cell.isMine) {
          cell.isFlagged = true;
        }
      }
    }
  }
};

// --- Âè≥ÈîÆÊ†áËÆ∞/ÂèñÊ∂àÊ†áËÆ∞ ---
const handleRightClick = (event: MouseEvent, r: number, c: number) => {
  event.preventDefault(); // ÈòªÊ≠¢ÈªòËÆ§Âè≥ÈîÆËèúÂçï
  // ‰øÆÂ§çÔºöÁ°Æ‰øùboard[r][c]Â≠òÂú®
  const row = board[r];
  if (!row) return;
  
  const cell = row[c];
  if (!cell) return;
  
  if (gameOver.value || gameWon.value || cell.isRevealed) return;
  cell.isFlagged = !cell.isFlagged;
};

// --- Ëß¶Êë∏‰∫ã‰ª∂Â§ÑÁêÜ ---
let longPressTimer: number | null = null;
let lastTouchCell: { r: number, c: number } | null = null;

const handleTouchStart = (event: TouchEvent, r: number, c: number) => {
  // Èò≤Ê≠¢È°µÈù¢Áº©Êîæ
  if (event.touches.length > 1) {
    event.preventDefault();
  }
  
  lastTouchCell = { r, c };
  
  // ËÆæÁΩÆÈïøÊåâÂÆöÊó∂Âô®
  longPressTimer = window.setTimeout(() => {
    // ÈïøÊåâÁõ∏ÂΩì‰∫éÂè≥ÈîÆÁÇπÂáª
    if (lastTouchCell) {
      const { r, c } = lastTouchCell;
      const row = board[r];
      if (!row) return;
      
      const cell = row[c];
      if (!cell) return;
      
      if (gameOver.value || gameWon.value || cell.isRevealed) return;
      cell.isFlagged = !cell.isFlagged;
      
      // Ê∑ªÂä†Ëß¶ËßâÂèçÈ¶àÔºàÂ¶ÇÊûúËÆæÂ§áÊîØÊåÅÔºâ
      if (window.navigator && window.navigator.vibrate) {
        window.navigator.vibrate(100);
      }
    }
    
    longPressTimer = null;
  }, 500); // 500ÊØ´ÁßíÈïøÊåâÂà§ÂÆö
};

const handleTouchEnd = (event: TouchEvent) => {
  // Â¶ÇÊûúÂÆöÊó∂Âô®Â≠òÂú®ÔºåËØ¥Êòé‰∏çÊòØÈïøÊåâÔºåÊòØÊôÆÈÄöÁÇπÂáª
  if (longPressTimer !== null) {
    clearTimeout(longPressTimer);
    longPressTimer = null;
    
    // ÊôÆÈÄöÁÇπÂáªÁõ∏ÂΩì‰∫éÂ∑¶ÈîÆÁÇπÂáªÔºåÊè≠Á§∫Ê†ºÂ≠ê
    if (lastTouchCell) {
      const { r, c } = lastTouchCell;
      revealCell(r, c);
    }
  }
  
  lastTouchCell = null;
};

const handleTouchMove = (event: TouchEvent) => {
  // Â¶ÇÊûúÊâãÊåáÁßªÂä®ÔºåÂèñÊ∂àÈïøÊåâ
  if (longPressTimer !== null) {
    clearTimeout(longPressTimer);
    longPressTimer = null;
  }
};

// --- ÈáçÁΩÆÊ∏∏Êàè ---
const resetGame = () => {
  initializeBoard();
};

// --- ÁîüÂëΩÂë®ÊúüÈí©Â≠ê ---
onMounted(() => {
  initializeBoard();
});
</script>

<template>
  <div class="minesweeper-game">
    <h2>Êâ´Èõ∑Ê∏∏Êàè üß®</h2>
    <div class="game-info">
      <p>Âú∞Èõ∑Êï∞: {{ MINES }}</p>
      <p v-if="gameOver">Ê∏∏ÊàèÁªìÊùüÔºÅË∏©Âà∞Âú∞Èõ∑‰∫Ü„ÄÇ</p>
      <p v-else-if="gameWon">ÊÅ≠Âñú‰Ω†ÔºåÊàêÂäüÊéíÈõ∑ÔºÅ</p>
      <button @click="resetGame">ÈáçÊñ∞ÂºÄÂßã</button>
      <div class="touch-instructions">
        <p>ÁßªÂä®ËÆæÂ§áÊìç‰ΩúËØ¥Êòé:</p>
        <p>- ÁÇπÂáª: Êè≠Á§∫Ê†ºÂ≠ê</p>
        <p>- ÈïøÊåâ: Ê†áËÆ∞/ÂèñÊ∂àÊ†áËÆ∞Âú∞Èõ∑</p>
      </div>
    </div>
    <div class="board" :class="{ 'game-over': gameOver, 'game-won': gameWon }">
      <div v-for="(row, r) in board" :key="r" class="row">
        <div
          v-for="(cell, c) in row"
          :key="c"
          class="cell"
          :class="{
            revealed: cell.isRevealed,
            mine: cell.isMine && cell.isRevealed,
            flagged: cell.isFlagged,
            [`n${cell.neighborMines}`]: cell.isRevealed && !cell.isMine
          }"
          @click="revealCell(r, c)"
          @contextmenu="handleRightClick($event, r, c)"
          @touchstart="handleTouchStart($event, r, c)"
          @touchend="handleTouchEnd"
          @touchmove="handleTouchMove"
        >
          <span v-if="cell.isFlagged">üö©</span>
          <span v-else-if="cell.isRevealed && cell.isMine">üí£</span>
          <span v-else-if="cell.isRevealed && cell.neighborMines > 0">{{ cell.neighborMines }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

